import React from 'react';
import "./styles/WelcomingMessage.css";
import Form from "./Form";

export default class WelcomingMessage extends React.Component{
    constructor(props){
        super(props);
        this.authors = "BIDAN Clment et PERNALON Paul"
        this.message = "Ralis par les tudiants de Centrale Marseille, ce site te permettra de dcouvrir la vie tudiante et associative de notre cole. En te souhaitant bon courage pour les concours et bonne dcouverte !";
    }

    render() {
        return(
            <div id="Message_WM">
                <p id={"pBaliseWM"}> &emsp; &emsp; {this.message} </p>
                <Form />
                <br/>
                <span> {this.authors} </span>
            </div>
        )
    }
}
